<template>
  <div>
    <!--    action="http://10.176.36.38:8443/api/scenarios/upload"-->
    <!--  ="http://localhost:8443/api/scenarios/upload"-->
    <div class="dialog-mask" v-show="myDialogVisible">
      <div class="dialog-container">
        <div class="dialog-body">
          <form class="modal">
            <span class="close" @click="handleCancel">X</span>
            <div class="content">
              <span class="title">上传场景文件</span>
              <p class="message">选择json格式的场景文件，点击上传可以检测场景文件是否有效</p>
              <el-upload
                class="button upload-btn"
                ref="upload"
                action="http://localhost:8443/api/scenarios/upload"
                :http-request="submitUpload"
                accept="application/json"
                :before-remove="deleteUploadedFile"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
                :file-list="fileList"
                :before-upload="beforeUpload"
                :auto-upload="false">
                <div class="button-container">
                  <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
                  <el-button style="margin-left: 10px;" size="small" type="success" @click="submit">确定</el-button>
                </div>
                <div slot="tip" class="el-upload__tip">只能上传json文件</div>
              </el-upload>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ScenarioUpload',
  data () {
    return {
      myDialogVisible: false,
      fileList: [],
      filePath: '',
      fileName: ''
    }
  },
  methods: {
    handleExceed (files, fileList) {
      let message = '当前限制选择1个文件，本次选择了' + files.length + '个文件'
      if (files.length + fileList.length >= 2) {
        message = message + '，共选择了' + (files.length + fileList.length) + '个文件'
      }
      this.$alert(message, '提示', {
        confirmButtonText: '确定'
      })
    },
    deleteUploadedFile () { // 删除上传的场景文件
      this.$refs.upload.uploadFiles.pop()
      if (this.filePath !== '') {
        const index = this.filePath.lastIndexOf('/') // lastIndexOf("/")  找到最后一个  /  的位置
        const fileName = this.filePath.substring(index + 1) // substr() 截取剩余的字符，即得带后缀的文件名
        let _this = this
        this.$axios.post('/scenarios/deleteuploadedfile', {
          fileName: fileName
        }).then(res => {
          _this.filePath = ''
          _this.fileList = []
          _this.fileName = ''
        }).catch(err => {
          console.log(err)
        })
      }
    },
    clear () {
      this.$refs.upload.clearFiles()
      this.fileName = ''
    },
    beforeUpload (file) {
      // 限制上传文件的类型和大小
      const isJson = (file.type === 'application/json')
      const isLimit5M = (file.size / 1024 / 1024 < 5)
      if (!isJson) {
        this.$alert('上传失败，仅支持上传json文件，请重新选择json格式的文件并上传！', '提示', {
          confirmButtonText: '确定'
        })
      }
      if (!isLimit5M) {
        this.$alert('上传失败，上传的文件大小不能超过5MB！', '提示', {
          confirmButtonText: '确定'
        })
      }
      this.fileName = file.name
      return isJson && isLimit5M
    },
    submitUpload (val) {
      let fileData = new FormData()
      fileData.append('file', val.file)
      this.$axios.post('/scenarios/upload', fileData)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            this.filePath = resp.data.result
            this.$alert('上传成功，该场景文件有效，请继续选择其他测试信息', '提示', {
              confirmButtonText: '确定'
            })
            this.$emit('onUpload')
            this.myDialogVisible = false
          } else {
            this.$alert(resp.data.message, '提示', {
              confirmButtonText: '确定'
            })
            this.$refs.upload.uploadFiles.pop()
          }
      })
    },
    submit () {
      this.$refs.upload.submit()
    },
    show () {
      this.myDialogVisible = true
    },
    handleCancel () {
      this.myDialogVisible = false
      this.clear()
    },
    handleConfirm () {
      this.myDialogVisible = false
      this.$emit('onUpload')
    }
  }
}
</script>

<style scoped>
/*对话框的样式*/
.dialog-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog-container {
  width: 400px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.dialog-body {
  padding: 16px;
}

.dialog-footer button {
  margin-left: 8px;
  padding: 8px 16px;
  border: none;
  background-color: #1890ff;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
}

.dialog-footer button:hover {
  background-color: #40a9ff;
}

/*上传文件的组件样式*/
.modal {
  background-color: rgb(255, 255, 255);
  border-radius: 1em;
  box-shadow: 0px 100px 48px -60px rgba(0,0,0,0.1);
  color: rgb(15, 14, 14);
  max-width: 400px;
  overflow: hidden;
  position: relative;
  transition: background-color 0.3s, color 0.3s;
}

.content > *, .modal > * {
  padding: 0.875em;
}

.title {
  font-size: 1.25em;
  font-weight: 600;
  line-height: 1.2;
  display: flex;
  justify-content: center;
}

.message {
  line-height: 1.2;
  text-align: center;
}

.upload-btn {
  background-color: transparent;
  border: 0.125rem dashed hsla(223,10%,50%,0.4);
  flex: 1;
  padding: 0.375rem 2rem;
}

.upload-btn:hover {
  background-color: hsla(223,10%,60%,0.2);
}

.close {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(226, 94, 54);
  height: 30px;
  width: 30px;
  border-radius: 50%;
  position: absolute;
  right: 10px;
  top: 10px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}
</style>



