<template>
  <div>
    <div class="dialog-mask" v-show="myDialogVisible">
      <div class="dialog-container">
        <div class="dialog-header">从场景库选取初始场景，输入场景名称以检索</div>
        <div class="dialog-body">
          <div class="scenario-search" style="display: flex; justify-content: space-between;">
            <el-input v-model="keyword" style="display: inline-block; margin-right: 10px;"></el-input>
            <el-button @click="search" style="display: inline-block;">检索场景</el-button>
          </div>
          <div class="search-result-container">
            <div v-show="resultScenarios.length === 0">
              <p>没有找到符合条件的场景</p>
            </div>
            <div class="search-result-left">
              <ul class="search-result-list">
                <li v-for="scenario in resultScenarios"
                    :key="scenario.name"
                    @click="showDetails(scenario)"
                    title="点击查看详情">
                  {{scenario.name}}
                </li>
              </ul>
            </div>
            <div class="search-result-right" v-if="selectedScenario">
              <div class="scenario-details-header">
                <span style="font-size: 30px;"><strong>{{selectedScenario.name}}</strong></span>
                <video controls width="200" :src="selectedScenario.video" type="video/mp4"></video>
              </div>
              <div class="scenario-details-body">
                <div><span>{{selectedScenario.intro}}</span></div>
                <div><span>车道类型:{{selectedScenario.drivewayType.type}}</span></div>
                <div><span>无人车规划任务:{{selectedScenario.taskPlanning.task}}</span></div>
                <div><span>障碍物类型:{{selectedScenario.obstacleType.type}}</span></div>
                <div><span>障碍物数量:{{selectedScenario.num}}</span></div>
              </div>
            </div>
          </div>
        </div>
        <div class="dialog-footer">
          <button @click="handleCancel">取消</button>
          <button @click="handleConfirm">确定</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ScenarioSearch',
  data () {
    return {
      myDialogVisible: false,
      resultScenarios: [],
      keyword: '',
      selectedScenario: null,
      selectedScenarioPath: '',
      selectedScenarioFileName: ''
    }
  },
  methods: {
    show () {
      this.myDialogVisible = true
    },
    search () {
      let _this = this
      this.selectedScenario = ''
      this.$axios.get('/scenarios/search-by-name-keyword/' + this.keyword)
        .then(resp => {
          if (resp && resp.data.code === 200) {
            _this.resultScenarios = resp.data.result
          }
        })
    },
    showDetails (scenario) {
      this.selectedScenario = scenario
      this.selectedScenarioPath = scenario.scenarioPath // 保存场景路径以便于其他组件使用
      this.selectedScenarioFileName = scenario.fileName // 保存场景名称以便于其他组件使用
    },
    handleCancel () {
      this.myDialogVisible = false
      this.clear()
    },
    handleConfirm () {
      this.myDialogVisible = false
      this.$emit('onSelectScenario')
      this.$alert('初始场景选择完毕，请继续选择其他测试信息', '提示', {
        confirmButtonText: '确定'
      })
    },
    // handleScenarioSelectFromLibrary () {
    //   var _this = this
    //   if (this.scenario_name === '') {
    //     this.$alert('请输入场景库中具体的场景名称', '提示', {
    //       confirmButtonText: '确定'
    //     })
    //   } else {
    //     this.$axios.get('/scenarios/searchbyname/' + this.scenario_name).then(resp => {
    //       if (resp && resp.data.code === 200) {
    //         _this.form.initial_scenario_path = resp.data.result
    //         // console.log(_this.form.initial_scenario_path)
    //         // console.log(resp.data.result)
    //         this.$alert('已找到该场景，请继续选择其他测试信息', '提示', {
    //           confirmButtonText: '确定'
    //         })
    //       } else {
    //         _this.form.initial_scenario_path = ''
    //         this.$alert(resp.data.message, '提示', {
    //           confirmButtonText: '确定'
    //         })
    //       }
    //     })
    //   }
    // },
    clear () {
      this.myDialogVisible = false
      this.resultScenarios = []
      this.keyword = ''
      this.selectedScenario = null
      this.selectedScenarioPath = ''
      this.selectedScenarioFileName = ''
    }
  }
}
</script>

<style scoped>

.dialog-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.dialog-container {
  width: 400px;
  background-color: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.dialog-header {
  padding: 16px;
  font-size: 18px;
  font-weight: bold;
  border-bottom: 1px solid #eee;
}

.dialog-body {
  padding: 16px;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
  padding: 8px;
  border-top: 1px solid #eee;
}

.dialog-footer button {
  margin-left: 8px;
  padding: 8px 16px;
  border: none;
  background-color: #1890ff;
  color: #fff;
  border-radius: 4px;
  cursor: pointer;
  outline: none;
}

.dialog-footer button:hover {
  background-color: #40a9ff;
}

.search-result-container {
  display: flex;
  width: 100%;
}

.search-result-left {
  flex: 1;
  padding: 10px;
  border-right: 1px solid #ccc;
  font-size: 15px;
  overflow: auto;
}

.search-result-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  font-size: 20px;
}

.search-result-right {
  flex: 1;
  font-size: 20px;
  margin-left: 20px;
}

.scenario-details-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10px;
}

.scenario-details-body {
  font-size: 15px;
  margin-left: 10px;
}

.search-result-list li {
  /*border: 1px solid #e1ebfb;*/
  background-color: white;
  padding: 5px;
}

.search-result-list li:hover {
  background-color: #e1ebfb;
  cursor: pointer;
}
</style>



